import {
    Dialog,
    DialogPanel,
    Disclosure,
    DisclosureButton,
    DisclosurePanel,
    Popover,
    PopoverButton,
    PopoverGroup,
    PopoverPanel,
  } from "@headlessui/react";
  import {
    ArrowPathIcon,
    Bars3Icon,
    ChartPieIcon,
    CursorArrowRaysIcon,
    FingerPrintIcon,
    SquaresPlusIcon,
    XMarkIcon,
  } from "@heroicons/react/24/outline";
  import {
    ChevronDownIcon,
    PhoneIcon,
    PlayCircleIcon,
  } from "@heroicons/react/24/solid";

import React from 'react';
const products = [
    {
      name: "All Items",
      href: "#",
      icon: ArrowPathIcon,
    },
    {
      name: "Mobiles",
      href: "#",
      icon: ChartPieIcon,
    },
    {
      name: "Charger",
      href: "#",
      icon: CursorArrowRaysIcon,
    },
    {
      name: "Watch",
      href: "#",
      icon: FingerPrintIcon,
    },
    {
      name: "Covers",
      href: "#",
      icon: SquaresPlusIcon,
    },
    {
      name: "Screen Protector",
      href: "#",
      icon: ArrowPathIcon,
    },
    {
      name: "Headphones",
      href: "#",
      icon: ArrowPathIcon,
    },
  ];

const callsToAction = [
  {
    name: "Contact Us",
    href: "#",
    icon: PhoneIcon,
  },
  {
    name: "Watch Demo",
    href: "#",
    icon: PlayCircleIcon,
  },
];

const Products = () => {
  return (
    <><Popover className="relative">
          <PopoverButton className="flex items-center gap-x-1 text-sm/6 font-semibold text-gray-900">
              Product
              <ChevronDownIcon
                  aria-hidden="true"
                  className="size-5 flex-none text-gray-400" />
          </PopoverButton>

          <PopoverPanel
              transition
              className="absolute left-0 z-10 mt-3 w-64 overflow-hidden rounded-3xl bg-white ring-1 shadow-lg ring-gray-900/5 transition data-closed:translate-y-1 data-closed:opacity-0 data-enter:duration-200 data-enter:ease-out data-leave:duration-150 data-leave:ease-in"
          >
              <div className="p-4">
                  {products.map((item) => (
                      <div
                          key={item.name}
                          className="group relative flex items-center gap-x-6 rounded-lg p-4 text-sm/6 hover:bg-gray-50"
                      >
                          <div className="flex size-11 flex-none items-center justify-center rounded-lg bg-gray-50 group-hover:bg-white">
                              <item.icon
                                  aria-hidden="true"
                                  className="size-6 text-gray-600 group-hover:text-indigo-600" />
                          </div>
                          <div className="flex-auto">
                              <a
                                  href={item.href}
                                  className="block font-semibold text-gray-900"
                              >
                                  {item.name}
                                  <span className="absolute inset-0" />
                              </a>
                          </div>
                      </div>
                  ))}
              </div>
              <div className="grid grid-cols-2 divide-x divide-gray-900/5 bg-gray-50">
                  {callsToAction.map((item) => (
                      <a
                          key={item.name}
                          href={item.href}
                          className="flex items-center justify-center gap-x-2.5 p-3 text-sm/6 font-semibold text-gray-900 hover:bg-gray-100"
                      >
                          <item.icon
                              aria-hidden="true"
                              className="size-5 flex-none text-gray-400" />
                          {item.name}
                      </a>
                  ))}
              </div>
          </PopoverPanel>
      </Popover>
            <Disclosure as="div" className="-mx-3">
              <DisclosureButton className="group flex w-full items-center justify-between rounded-lg py-2 pr-3.5 pl-3 text-base/7 font-semibold text-gray-900 hover:bg-gray-50">
                  Inventoy
                  <ChevronDownIcon
                      aria-hidden="true"
                      className="size-5 flex-none group-data-open:rotate-180" />
              </DisclosureButton>
              <DisclosurePanel className="mt-2 space-y-2">
                  {products.map((item) => (
                      <DisclosureButton
                          key={item.name}
                          as="a"
                          href={item.href}
                          className="block rounded-lg py-2 pr-3 pl-6 text-sm/7 font-semibold text-gray-900 hover:bg-gray-50"
                      >
                          {item.name}
                      </DisclosureButton>
                  ))}
              </DisclosurePanel>
          </Disclosure></>
  )
}
export default Products;